import React from "react";

function Category({ setCategory }) {
  const changeCategory = (e) => {
    setCategory(e.target.value);
  };
  return (
    <select
      onChange={changeCategory}
      style={{
        padding: "6px 8px",
        height: "32px",
        transform: "translateY(-1px)",
      }}
    >
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
      <option value=""></option>
    </select>
  );
}

export default Category;
